"use client";

import { useState } from 'react';
import useSWR, { mutate } from 'swr';
import { Plus, Trash2, GripVertical } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

const fetcher = (url: string) => fetch(url).then(res => res.json());

export default function SkillsPage() {
    const { data: skills, error, isLoading } = useSWR('/api/skills', fetcher);
    const [newSkill, setNewSkill] = useState("");
    const [isSubmitting, setIsSubmitting] = useState(false);

    const handleAdd = async (e: React.FormEvent) => {
        e.preventDefault();
        if (!newSkill.trim()) return;

        setIsSubmitting(true);
        try {
            const res = await fetch('/api/skills', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ name: newSkill })
            });

            if (res.ok) {
                setNewSkill("");
                mutate('/api/skills');
            }
        } catch (error) {
            console.error(error);
        } finally {
            setIsSubmitting(false);
        }
    };

    const handleDelete = async (id: string) => {
        if (!confirm('Delete this skill?')) return;
        try {
            await fetch(`/api/skills/${id}`, { method: 'DELETE' });
            mutate('/api/skills');
        } catch (error) {
            console.error(error);
        }
    };

    if (isLoading) return <div className="text-secondary">Loading skills...</div>;

    return (
        <div className="max-w-2xl mx-auto space-y-8">
            <h1 className="text-3xl font-display font-bold text-white">Manage Skills</h1>

            {/* Add Skill Form */}
            <div className="bg-surfaceHighlight p-6 rounded-2xl border border-white/5">
                <form onSubmit={handleAdd} className="flex gap-4">
                    <input
                        type="text"
                        value={newSkill}
                        onChange={(e) => setNewSkill(e.target.value)}
                        placeholder="New Skill (e.g. React, Next.js)"
                        className="flex-1 bg-background border border-white/10 rounded-xl px-4 py-3 text-white focus:outline-none focus:border-accent transition-colors"
                    />
                    <button
                        type="submit"
                        disabled={isSubmitting || !newSkill.trim()}
                        className="px-6 py-3 bg-accent text-black font-bold rounded-xl hover:bg-white transition-colors disabled:opacity-50"
                    >
                        <Plus size={20} />
                    </button>
                </form>
            </div>

            {/* Skills List */}
            <div className="space-y-3">
                <AnimatePresence>
                    {skills?.map((skill: any) => (
                        <motion.div
                            key={skill._id}
                            initial={{ opacity: 0, y: 10 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, height: 0 }}
                            className="flex items-center justify-between p-4 bg-surfaceHighlight/50 border border-white/5 rounded-xl group"
                        >
                            <div className="flex items-center gap-4">
                                <div className="p-2 bg-white/5 rounded-lg text-secondary cursor-grab active:cursor-grabbing">
                                    <GripVertical size={16} />
                                </div>
                                <span className="text-lg font-medium text-white">{skill.name}</span>
                            </div>
                            <button
                                onClick={() => handleDelete(skill._id)}
                                className="p-2 text-secondary hover:text-red-500 hover:bg-red-500/10 rounded-lg transition-all"
                            >
                                <Trash2 size={18} />
                            </button>
                        </motion.div>
                    ))}
                    {skills?.length === 0 && (
                        <div className="text-center text-secondary py-12">No skills added yet.</div>
                    )}
                </AnimatePresence>
            </div>
        </div>
    );
}
